<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                重置密码
            </p>
        </section>
        <div class="con-con">
            <div class="list">
                <img src="../../assets/login/registered_icon_phone@2x.png" slot="left" alt="">
                <md-input-item
                        title="邮箱用户名"
                        ref="input11"
                        :clearable="true"
                        v-model="userInfo.username"
                        placeholder="请输入邮箱用户名"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img src="../../assets/login/registered_icon_code@2x.png" slot="left" alt="">
                <md-input-item
                        title="新密码"
                        ref="input11"
                        :clearable="true"
                        v-model="userInfo.password"
                        placeholder="请输入新密码"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img src="../../assets/login/registered_icon_code@2x.png" slot="left" alt="">
                <md-input-item
                        title="确认密码"
                        ref="input11"
                        :clearable="true"
                        v-model="againPassword"
                        placeholder="请确认密码"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img class="verificationcode" src="../../assets/login/registered_icon_verificationcode@2x.png" slot="left" alt="">
                <md-input-item
                        title="验证码"
                        ref="input11"
                        v-model="userInfo.code"
                        :clearable="true"
                        placeholder="请输入验证码"
                >
                    <div v-if="!isSendShow" class="input-operator" slot="right" @click="sendCode">获取验证码</div>
                    <div v-if="isSendShow" class="min" slot="right">{{ num }}秒</div>
                </md-input-item>
            </div>
        </div>
        <input class="md-button" type="button" value="立即重置" @click="onRegister">
    </main>
</template>

<script>
import { Field, CellItem, Toast, InputItem, Icon, Button } from 'mand-mobile'
import { sendCode, resetPsw } from '../../apiUrl/login'
export default {
  name: 'index',
  components: {
    [Field.name]: Field,
    [Button.name]: Button,
    [InputItem.name]: InputItem,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon
  },
  data () {
    return {
      userInfo: {
        roleId: '',
          username: '',
        password: '',
        code: ''
      },
      num: 60,
      isSendShow: false,
      againPassword: ''
    }
  },
  methods: {
    // 回退
    goBack () {
      this.$router.push('/login')
    },
    sendCode () {
      if (this.userInfo.username) {
        const args = {
            username: this.userInfo.username
        }
        sendCode(args).then((res) => {
          if (res.statusMsg === 'success') {
            Toast.succeed('短信已发送！')
          }
        })
        this.isSendShow = true
        const val = setInterval(() => {
          this.num--
          if (this.num === 0) {
            clearTimeout(val)
            this.isSendShow = false
            this.num = 60
          }
        }, 1000)
      } else {
        Toast.failed('手机号格式不正确')
      }
    },
    onRegister () {
      if (this.userInfo.password === this.againPassword) {
        resetPsw(this.userInfo).then((res) => {
          if (!res.message) {
            Toast.succeed('修改成功！')
            this.$router.push('/login')
          } else {
            Toast.failed(res.message)
          }
        })
      } else {
        Toast.failed('两次密码输入不一致')
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
    .input-operator
        text-align center
        width 150px
        height 50px
        color #fff
        font-size 18px!important
        line-height 50px
        margin-right 20px
        background:linear-gradient(0deg,rgba(202,2,57,1),rgba(255,97,53,1));
        border-radius:4px;
    .min
        width 150px
        height 50px
        color #999
        background #E5E8EE
        font-size 18px!important
        line-height 50px
        margin-right 20px
    .con-con
        width 90%
        margin 0px auto
        margin-top 150px
        background:rgba(255,255,255,1);
        box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
        border-radius:12px;
        .list
            img
                width 22px
                height 30px
                float left
                margin 30px 22px auto 32px
    /deep/.md-cell-item-body
        justify-content space-between!important
        padding 0px 20px
        text-align left
    /deep/.md-cell-item-content
        flex: 1 1 0%!important
    /deep/.md-cell-item-left
        margin-right 10px
    /deep/.md-cell-item
        height 91px
    /deep/.md-cell-item-body:before
        top:91px
        bottom auto
    /deep/.md-button
        width:660px;
        height:88px;
        line-height: 88px;
        background:#021955FF;
        border-radius:12px;
        margin 0 auto
        color #fff
        margin-top 60px
        font-size 32px
    /deep/.md-field-item-content
        text-align left
    /deep/.verificationcode
            width 27px!important
            height 30px!important
            margin-top 32px!important
</style>
